	module.exports = {"data":{"markdownRemark":{"html":"<h2>◆フリー・同人・商業ゲーム作者**</h2>\n<p><a href=\"http://d.hatena.ne.jp/yaneurao/\">やねうらお</a>氏、Ｓｈｏｕ氏(<a href=\"http://izeef.web.fc2.com/izeef/izeef.html\">izeef</a>)、ZUN氏(<a href=\"http://www16.big.or.jp/~zun/\">上海アリス幻樂団</a>) 私がXG信者からGS信者に改宗したのは後者お二方の影響が大きい。\n<strong>◆漫画家</strong>\n富沢ひとし氏、福本伸行氏、うめてんてー(なぜか変換できない)</p>\n<h2>◆音楽ジャンル</h2>\n<p>ラウンジポップ、ハードテクノ、トランス(サイケ/ハンズアップどっちも好き)、ガバ(ハコでは首壊れるから聞きたくない)</p>\n<h2>◆漫画・アニメ</h2>\n<p>無頼伝 涯、ミルク クローゼット、BIRTH(OVA)、ひだまりスケッチ(<a href=\"http://youtu.be/1Cn-Gz1rvjM\">capsuleのMAD</a>で初めて知ったｗ)、魔法少女まどか☆マギカ(うめてんてー繋がりで) **◆ゲーム</p>\n<ul>\n<li>東方Project。妖々夢が初でそこから全作揃えました。あぁ音楽CDについては、幺樂団はスルー、秘封倶楽部関係は最近のやつは資金難で買えてない。STGスキルは全盛期は妖々夢Phantasmクリア、Hardをギリギリクリアできる程度の能力でしたが、今は超絶劣化中。</li>\n<li>DanceDanceRevolution。<strong>ID: 11121133</strong>。1stのIR対応版から始めて、真面目に攻略していたのは4th+辺りまで。MAXが出た前後で引退。それ以降はたまに付き合いでやる程度。EXPART譜面を2曲連続で踏む体力ありません。</li>\n<li>beatmania(5鍵)。2nd(PS版)が最初で、AC版は3rdが最初。真面目に攻略していたのはClubMIX辺りまで、CORE REMIXを最後にIIDXへと移行した。</li>\n<li>beatmaniaIIDX。<strong>ID: 8448-5915</strong>。やっていたのは1st～6th初期、EMP後期～RA初期、tricoroのみ。本格的に攻略始めたのはsubstream辺りから。1P正規厨、北斗捨てて1048に移行したもののまだ慣れない。</li>\n<li>RAGNAROK Online。2003～2004頃にハマりました。コンビニでバイト中にデジキューブの納品があって、イラストがかわいいのでついジャケット買いしたのが地獄の始まり、ほんの一瞬で1年が過ぎてしまいました。また2007年ごろに魔が差して再度始めて見たが最期、半年間が一瞬で吹っ飛びました。それ以降、こういう類のMMORPGは恐ろしいので封印しています。</li>\n<li>Final Fantasy 1～7。真面目に攻略していたのは6まで。7はDISC2に行く前に萎えた。8は体験版だけやっておしまい。FF2のWindows Phone移植はまだか、まだなのか。</li>\n<li>DOOM。IIIと64、PS版以外。4～5年置きに一度くらい再攻略したい病が発動して困る。初代だけなら1週間コースだが、IIとFinalとEternal含めるとガチで3か月コースだからホント困る。</li>\n<li>Unreal Tournament。初代(99)と2004、3は購入したものの<del>積み状態</del>もとい、10円で売却した。これも数年おきに再攻略したい病が発動して困る。</li>\n<li>Minecraft。元々マルチプレイのサーバ運営だけやっていてゲーム自体には大して興味持っていなかったのですが、レッドストーン回路使ったギミックとか工業MODやコンピュータMODなどの動画に毒されて現在進行形でハマり中。</li>\n</ul>","frontmatter":{"cover":null,"date":"2012-11-27T22:10:14+09:00","strDate":"2012/11/27","title":"代表が好きなor影響を受けた○○○○○"}}},"pathContext":{"path":"/ja/blog/2012/11/27-代表が好きなor影響を受けた○○○○○/","langKey":"ja"}}

